import useWindowWidth from '@cetus/hooks/src/useWindowWidth'
import { d, Decimal, formatNumber } from '@cetus/utils'
import { Box, Center, Flex, HStack, Image, Skeleton, Spinner, Text, VStack } from '@chakra-ui/react'
import { useEffect, useMemo, useRef, useState } from 'react'
import Comma from './Comma'
import icon_currentprice from '/images/icon_vault_currentprice@2x.png'

type PositionPriceRangeProps = {
  displayMaxPrice?: string
  displayMinPrice?: string
  displayPrice?: string
  isLoading?: boolean
}

export default function PositionPriceRange(props: PositionPriceRangeProps) {
  const { displayMaxPrice, displayMinPrice, displayPrice, isLoading } = props
  const { isApp } = useWindowWidth()
  const fallbackWidth = isApp ? 200 : 366
  const [currentElementWidth, setCurrentElementWidth] = useState<number>(0)
  const currentElementRef = useRef<HTMLDivElement>(null)

  const actualWidth = currentElementWidth || fallbackWidth

  const positionPriceRangeLoading = useMemo(() => {
    return !(displayMinPrice && displayMaxPrice && displayPrice)
  }, [displayMaxPrice, displayMinPrice, displayPrice])

  useEffect(() => {
    const getElementWidth = () => {
      if (currentElementRef.current) {
        setCurrentElementWidth(currentElementRef.current.offsetWidth)
      }
    }

    getElementWidth()

    const resizeObserver = new ResizeObserver(getElementWidth)
    if (currentElementRef.current) resizeObserver.observe(currentElementRef.current)

    return () => {
      if (currentElementRef.current) resizeObserver.unobserve(currentElementRef.current)
    }
  }, [positionPriceRangeLoading])

  const isActive = useMemo(() => {
    if (displayMaxPrice === '‚àû') return true
    if (displayPrice && displayMinPrice && displayMaxPrice) {
      if (d(displayPrice).greaterThanOrEqualTo(displayMaxPrice)) return false
      if (d(displayMinPrice).greaterThanOrEqualTo(displayPrice)) return false
    }
    return true
  }, [displayPrice, displayMinPrice, displayMaxPrice])

  const sliderML = useMemo(() => {
    if (d(actualWidth).gt(0)) {
      if (displayMaxPrice === '‚àû') return `calc(50% - ${actualWidth / 2}px)`
      if (displayPrice && displayMinPrice && displayMaxPrice) {
        if (d(displayPrice).lessThanOrEqualTo(displayMaxPrice) && d(displayPrice).greaterThanOrEqualTo(displayMinPrice)) {
          const relativePosition = d(displayPrice)
            .sub(displayMinPrice)
            .div(d(displayMaxPrice).sub(d(displayMinPrice)))
            .mul(100)

          return `calc(${relativePosition}% - ${actualWidth / 2}px)`
        }
      }
      return `calc(50% - ${actualWidth / 2}px)`
    }
  }, [displayPrice, displayMinPrice, displayMaxPrice, actualWidth])

  return (
    <>
      {!positionPriceRangeLoading ? (
        <Flex w="100%" h="120px" position="relative">
          <HStack w="100%" alignItems="center" justify="center" position="absolute" left="0" top="50%">
            {/* Min Price */}
            <VStack align="flex-end" gap="6px" maxW={{ md: '48%', lg: 'auto' }} zIndex="9999">
              <Text fontSize="12px">Min Price</Text>
              {isLoading ? (
                <Flex h="18px" align="center">
                  <Skeleton />
                </Flex>
              ) : (
                <Text fontSize="14px" fontWeight="500" color="text_caption">
                  <Comma value={formatNumber(displayMinPrice, 6, true, Decimal.ROUND_DOWN)} />
                </Text>
              )}
            </VStack>

            <Center zIndex="99" position="relative" h="40px" w="366px">
              <Center
                w="100%"
                h="100%"
                borderRadius="8px"
                bg={isActive ? 'linear-gradient( 270deg, #003F68 0%, #006E51 100%)' : 'primary_opacity.10'}
              />
              <VStack
                gap="8px"
                mt="-48px"
                ref={currentElementRef}
                ml={isActive ? '-2px' : '0px'}
                position="absolute"
                left={sliderML}
                zIndex="999"
                // w={`${actualWidth}px`}
                transition="left 0.3s ease"
              >
                {/* Current Price p="6px 24px" */}
                <VStack p="6px 0px" borderRadius="8px" gap="4px" transition="all 0.3s ease">
                  <Text fontSize="xs" h="14px" whiteSpace="nowrap">
                    Current Price
                  </Text>
                  {isLoading ? (
                    <Flex h="18px" align="center">
                      <Skeleton />
                    </Flex>
                  ) : (
                    <HStack>
                      <Text fontSize="16px" color="text_highlight" whiteSpace="nowrap">
                        <Comma value={formatNumber(displayPrice, 6, true, Decimal.ROUND_DOWN)} />
                      </Text>
                    </HStack>
                  )}
                </VStack>

                <VStack zIndex="100">
                  <Box w="2px" h="42px" bg={isActive && displayMaxPrice !== '‚àû' ? '#fff' : 'transparent'} />
                  {isActive && displayMaxPrice !== '‚àû' && <Image src={icon_currentprice} w="10px" h="6px" mt="-6px" />}
                </VStack>
              </VStack>
            </Center>

            {/* Max Price */}
            <VStack align="flex-start" gap="6px" maxW={{ md: '48%', lg: 'auto' }}>
              <Text fontSize="12px">Max Price</Text>
              {isLoading ? (
                <Flex h="18px" align="center">
                  <Skeleton />
                </Flex>
              ) : (
                <Text fontSize="14px" fontWeight="500" color="text_caption">
                  <Comma value={formatNumber(displayMaxPrice, 6, true, Decimal.ROUND_DOWN)} />
                </Text>
              )}
            </VStack>
          </HStack>
        </Flex>
      ) : (
        <Flex h="120px" width="100%" align="center" position="relative">
          <Box position="absolute" top="50%" left="50%" transform="translate(-50%,-50%)">
            <Spinner />
          </Box>
        </Flex>
      )}
    </>
  )
}
