import useDisplayPrice from '@/hooks/common/useDisplayPrice'
import useDisplayTickPrice from '@/hooks/common/useDisplayTickPrice'
import { Block, SelectTab } from '@cetus/design'
import { Tab } from '@cetus/design/src/components/common/SelectTab'
import { Token } from '@cetus/types'
import { PositionPriceRange } from '@cetus/ui-kit'
import { HStack, Text, VStack } from '@chakra-ui/react'
import { useEffect, useMemo, useState } from 'react'

// VaultËØ¶ÊÉÖ ‰ª∑Ê†ºËåÉÂõ¥
export function PositionRange({
  currentPrice,
  minPrice,
  maxPrice,
  displayTokenA,
  displayTokenB
}: {
  currentPrice?: string
  minPrice?: string
  maxPrice?: string
  displayTokenA?: Token
  displayTokenB?: Token
}) {
  const [changeSide, setChangeSide] = useState<boolean>(false)
  const { displayPrice } = useDisplayPrice({ price: currentPrice, changeSide })
  const { displayMaxPrice, displayMinPrice } = useDisplayTickPrice({ minPrice, maxPrice, changeSide })
  console.log('üöÄüöÄüöÄ ~ PositionRange.tsx:27 ~ PositionRange ~ changeSide:', changeSide)

  const baseCoin = useMemo(() => {
    return !changeSide ? displayTokenA : displayTokenB
  }, [displayTokenA, displayTokenB, changeSide])

  const rangeTabList = useMemo(() => {
    if (displayTokenA && displayTokenB) {
      console.log('üöÄ ~ rangeTabList ~ rangeTabList:', displayTokenA)
      return [displayTokenA, displayTokenB]?.filter(Boolean).map(item => ({
        label: item?.symbol,
        key: item?.coin_type,
        isToken: true,
        imgInfo: {
          src: item?.logo_url,
          w: '16px',
          h: '16px',
          coinType: item ? item?.coin_type : '',
          showTagWidth: '8px',
          showTagHeight: '8px'
        }
      }))
    } else {
      return []
    }
  }, [displayTokenA?.coin_type, displayTokenB?.coin_type])

  const [currentRangeTab, setCurrentRangeTab] = useState<string>()

  const onReverseClick = (item?: Tab) => {
    setChangeSide(!changeSide)
    if (item && item?.coin_type) {
      setCurrentRangeTab(item?.coin_type)
    } else {
      setCurrentRangeTab(rangeTabList?.find(tab => tab.key !== currentRangeTab)?.key)
    }
  }
  useEffect(() => {
    if (baseCoin) {
      setCurrentRangeTab(baseCoin?.coin_type)
    }
  }, [baseCoin])
  return (
    <Block border="none" mt={{ base: '12px', lg: '0' }} p={{ base: '16px 8px', lg: '15px 16px 16px' }} position="relative" height="190px">
      <VStack w="100%">
        <HStack w="100%" justifyContent="space-between">
          <Text fontSize="16px" color="text_caption">
            Position Range
          </Text>
          {/* ÂàáÊç¢base */}
          <HStack gap="0px">
            {/* {logoUrl && (
              <SingleCoinImage
                imageUrl={logoUrl}
                imageStyle={{
                  w: '20px',
                  h: '20px'
                }}
              />
            )}
            <Text color="text_caption"> {baseCoin?.symbol}</Text> */}
            {/* <HStack ml="8px" gap="4px" cursor="pointer" onClick={()=>setChangeSide(!changeSide)}> */}
            {/* <Text fontSize="12px"> Base coin</Text>
              <Icon xlinkHref="#icon-icon_swap1" /> */}
            {rangeTabList?.length > 0 && (
              <SelectTab<any, any>
                type="outlineTab"
                tabList={rangeTabList}
                currentTab={currentRangeTab}
                handleChangeTab={tab => onReverseClick(tab)}
                wrapStyle={{
                  h: '32px',
                  p: '3px',
                  border: '1px solid',
                  borderColor: 'border',
                  borderRadius: '8px',
                  gap: '4px',
                  zIndex: '99'
                }}
                itemStyle={{
                  h: '24px',
                  p: '4px 8px',
                  borderRadius: '4px',
                  gap: '4px'
                }}
              />
            )}
            {/* </HStack> */}
          </HStack>
        </HStack>

        <PositionPriceRange displayPrice={displayPrice} displayMaxPrice={displayMaxPrice} displayMinPrice={displayMinPrice} />
      </VStack>
    </Block>
  )
}
