import useCalculateVaultFarmingApr from '@/hooks/vaults-farming/useCalculateVaultFarmingApr'
import useCurrentVaultsFarm from '@/hooks/vaults-farming/useCurrentVaultsFarm'
import { VaultV2ApiInfo } from '@/types/vaults-v2'
import { AddressCopyLink, CetusTooltip, SelectTab, TooltipIcon } from '@cetus/design'
import useExplorer from '@cetus/hooks/src/useExplorer'
import useWindowWidth from '@cetus/hooks/src/useWindowWidth'
import { HTextLabelBox } from '@cetus/ui-kit'
import { symbolDataDisplayProcessing } from '@cetus/utils'
import { Center, HStack, Stack, Text, VStack } from '@chakra-ui/react'
import VaultsAprBlock from '../list/common/VaultsAprBlock'
import { CoinImage } from '../list/common/VaultsRewardsBlock'
import VaultsPoolSelect from './VaultsPoolSelect'

type VaultsHeaderProps = {
  apiVaultInfo: VaultV2ApiInfo
  vaultTvl: string
  performanceFee: string
  vaultTabList: any
  vaultCurrTab: string
  setVaultCurrTab: (item: any) => void
  vaultsList?: any
}

export function VaultsHeader({ apiVaultInfo, vaultTvl, performanceFee, vaultTabList, vaultCurrTab, setVaultCurrTab, vaultsList }: VaultsHeaderProps) {
  const { isApp } = useWindowWidth()
  const { getExplorerUrl } = useExplorer()
  const { currentVaultsFarm, isVaultsFarming } = useCurrentVaultsFarm(apiVaultInfo?.vaultId)
  const { vaultFarmingAprDisplay } = useCalculateVaultFarmingApr(currentVaultsFarm)

  return (
    <Stack
      mb="16px"
      flexDir={{ base: 'column', lg: 'row' }}
      w="100%"
      alignItems="flex-end"
      justifyContent="space-between"
      gap={{ base: '12px', lg: '8px' }}
    >
      <VStack w={{ base: '100%', lg: 'unset' }} gap="4px" align="flex-start">
        <VaultsPoolSelect isVaultsFarming={isVaultsFarming} apiVaultInfo={apiVaultInfo} vaultsList={vaultsList} setVaultCurrTab={setVaultCurrTab} />
        {!isApp && (
          <SelectTab
            type="borderTab"
            wrapStyle={{
              w: '100%',
              h: '60px',
              bg: 'none',
              border: 'none'
            }}
            itemStyle={{
              fontSize: '16px',
              mr: '28px'
            }}
            tabList={vaultTabList}
            currentTab={vaultCurrTab}
            handleChangeTab={(item: any) => {
              setVaultCurrTab(item?.value)
            }}
          />
        )}
      </VStack>
      {/* Âè≥ËæπÂå∫Âüü */}
      <Stack w={{ base: '100%', lg: 'unset' }} flexDir={{ base: 'column', lg: 'row' }} gap={{ base: '8px', lg: '28px' }}>
        {isApp && (
          <HTextLabelBox
            label="Vault Address"
            labelStyle={{
              fontSize: '14px'
            }}
            value={
              <AddressCopyLink
                fontSize="14px"
                color="text_caption"
                address={apiVaultInfo?.vaultId || ''}
                onClickLink={() => {
                  window.open(getExplorerUrl(apiVaultInfo?.vaultId, 'poolAddress'), '_blank')
                }}
              />
            }
          />
        )}
        <VStack w={{ base: '100%', lg: 'unset' }} justify="space-between" gap="6px" flexDirection={{ base: 'row', lg: 'column' }}>
          {!isApp ? (
            <>
              <Text w="100%" fontSize={{ base: '14px', lg: '12px' }} textAlign={isApp ? 'left' : 'right'}>
                APY
              </Text>
              <VaultsAprBlock
                vaultId={apiVaultInfo?.vaultId}
                wrapStyle={{
                  flexDirection: 'row',
                  h: '20px'
                }}
                farmingTextStyle={{
                  p: '4px'
                }}
              />
            </>
          ) : (
            <HTextLabelBox
              wrapStyle={{
                w: '100%',
                gap: '6px'
              }}
              label="APY"
              labelStyle={{
                fontSize: '14px',
                fontWeight: '500'
              }}
              value={
                <VaultsAprBlock
                  vaultId={apiVaultInfo?.vaultId}
                  wrapStyle={{
                    flexDirection: 'row',
                    alignItems: 'center'
                  }}
                  farmingTextStyle={{
                    p: '0 6px'
                  }}
                />
              }
              valueStyle={{
                color: 'text_highlight',
                h: '20px',
                lineHeight: '20px',
                fontSize: '14px',
                fontWeight: '500',
                textDecoration: 'none'
              }}
            />
          )}
        </VStack>
        <VStack w={{ base: '100%', lg: 'unset' }} justify="space-between" gap="6px" flexDirection={{ base: 'row', lg: 'column' }}>
          <Text w="100%" fontSize={{ base: '14px', lg: '12px' }} textAlign={isApp ? 'left' : 'right'}>
            Earnings
          </Text>
          <CetusTooltip
            placement="top"
            tooltip={
              <Text fontSize="12px" lineHeight="20px" maxW="240px">
                Rewards will be auto harvested and compounded to your position.
              </Text>
            }
          >
            <Center>
              <HStack justifyContent="end">
                {apiVaultInfo?.vaultsRewards.map(coinType => {
                  return <CoinImage key={coinType} coinType={coinType} />
                })}
              </HStack>
            </Center>
          </CetusTooltip>
        </VStack>
        <VStack w={{ base: '100%', lg: 'unset' }} justify="space-between" gap="6px" flexDirection={{ base: 'row', lg: 'column' }}>
          <Text w="100%" fontSize={{ base: '14px', lg: '12px' }} textAlign={isApp ? 'left' : 'right'}>
            TVL
          </Text>
          <Text color="text_caption" h="20px" lineHeight="20px" fontWeight="500">
            {(apiVaultInfo?.category == 'haedal' || apiVaultInfo?.category == 'cetus-dlmm'
              ? symbolDataDisplayProcessing(vaultTvl, '$')
              : apiVaultInfo?.vaultsTvlDisplay) || '-'}
          </Text>
        </VStack>
        {apiVaultInfo?.category == 'haedal' ||
          (apiVaultInfo?.category == 'cetus-dlmm' && (
            <VStack
              w={{ base: '100%', lg: 'unset' }}
              justify="space-between"
              align={{ base: 'center', lg: 'flex-end' }}
              gap="6px"
              flexDirection={{ base: 'row', lg: 'column' }}
            >
              <HStack gap="4px" mt="-4px">
                <Text w="100%" fontSize={{ base: '14px', lg: '12px' }}>
                  Performance Fee
                </Text>
                <TooltipIcon maxW="340px" tooltipCon="Commission charged from earned fees and rewards" />
              </HStack>
              <Text color="text_caption" h="20px" lineHeight="20px" fontWeight="500">
                {performanceFee || '-'}
              </Text>
            </VStack>
          ))}
      </Stack>
    </Stack>
  )
}
